.main {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 16px;
  padding-left: 12px;
  .title {
    display: flex;
    justify-content: space-between;
    max-width: 95%;
    align-items: center;
    padding: 0px 24px;
    color: white;
    h1 {
      font-size: 48px;
    }
    a {
      font-size: 24px;
    }
  }
  .cardBlock {
    display: flex;
    max-height: 100%;
    height: 100%;
    padding-left: 24px;
    gap: 50px;
    flex-wrap: wrap;
  }
}
